/*Positioning CSS in the .App class*/
.App{
  position: relative;
  height: 100vh;
  overflow: hidden;
}
.App-header{
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-bottom: 5px;
}


.content{
  padding: 2vh 2vw;
  overflow: auto;
  height: 55vh;
}

